import SpotifyWebApi from 'spotify-web-api-node';

export async function initClient(code: string) {
	const spotifyApi = new SpotifyWebApi({
		clientId: '0916ebedc8fe494699b4252738eb2444',
		clientSecret: 'bf470b8b094245afb8799dcfa33f2645',
		redirectUri: 'http://localhost:3000/my-playlists',
	});

	spotifyApi.clientCredentialsGrant();
	const token = await spotifyApi.authorizationCodeGrant(code);
	spotifyApi.setAccessToken(token.body.access_token);
	spotifyApi.setRefreshToken(process.env.REFRESH_TOKEN as string);
	console.log('ðŸš€ ~ refresh_token:', token.body.refresh_token);
	console.log('ðŸš€ ~ access_token:', token.body.access_token);

	return spotifyApi;
}
